.App {
  text-align: center;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.card {
  width: 100px;
  height: 150px;
  border: 3px solid black;
  border-radius: 3px;
  size: 10px;
  margin: 5px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}
.cardGrid{
  width: 600px;
  background-color: rgb(186, 208, 248);
  /* display: grid; */
  /* grid-template-columns: repeat(5, 1fr); */
  border: 4px solid black;
}
.deck{
  width: 600px;
  background-color: rgb(189, 220, 202);
  /* display: grid; */
  /* grid-template-columns: repeat(5, 1fr); */
  border: 4px solid black;
}
.button{
  border: 1px solid rgb(75, 75, 75);
  background-color: rgb(28, 177, 236);
  border-radius: 5px;
  color: white;
  font-size: 20px;
  padding: 10px;
  margin: 10px;
}